<template>
    <div class="rank">
        <mt-cell v-for="(list,index) in songList" :to="`/rank/info/${list.rankid}`" :key="index" :title="list.rankname" is-link>
            <img slot="icon" :src="list.imgurl.replace('{size}', '400')" width="60" height="60">
        </mt-cell>
    </div>
</template>

<script>
import { Indicator } from 'mint-ui';

export default {
    data(){
        return {
            songList:[]
        }
    },
    created(){
        this.getList();
    },
    methods:{
        getList(){
            Indicator.open({
                text: '加载中...',
                spinnerType: 'snake'
            });
            this.$http.get('/indexList/rank/list&json=true').then(({data}) => {
                Indicator.close();
                this.songList = data.rank.list;
            });
        }
    }
}
</script>

<style type="lang">
.rank{
    .mint-cell-title img {
        margin-right: 10px;
    }
    .mint-cell-wrapper{
        font-size: 16px;
    }   
}
</style>


