<template>
    <div v-show="detailPlayerFlag">
        <div class="detail_player" :style="{backgroundImage:`url(${audio.imgUrl})`}"></div>
        <div class="detail_player" :style="{backgroundImage:`url(${audio.imgUrl})`,filter: 'blur(5px)', '-webkit-filter':'blur(5px)'}"></div>
        <div class="detail_player-content">
            <div class="detail_player-title container">
                <span class="detail_player-back" @click="hideDetailPlayer()"></span>
                {{audio.title}}
            </div>
            <div class="detail_player-img">
                <img :src="audio.imgUrl">
            </div>
            <div class="detail_player-lrc">
                <div class="lrc-content" :style="{marginTop: lrcOffset + 'px'}">
                    <p v-for="(item,index) in songLrc" :class="{isCurrentLrc:item.seconds >= audio.currentLength ,disCurrentLrc:item.seconds < audio.currentLength}">
                        {{item.lrcContent}}
                    </p>
                </div>
            </div>
            <div class="detail_player-range container">
                <span class="detail_player-time">{{ audio.currentLength | time }}</span>
                <!-- input 事件会一直触发,所以需要禁用 range -->
                <mt-range id="range"
                    :min="0"
                    :max="audio.songLength"
                    :bar-height="3"
                    v-model="audio.currentLength"
                   @click.native="rangeChange($event)" disabled style="width: 80%">
                </mt-range>
                <span class="detail_player-time">{{ audio.songLength | time }}</span>
            </div>
            <div class="detail_player-control player-padding">
                <i class="detail_player-btn play-prev player_btn-sm" @click="prev()"></i>
                <i class="detail_player-btn play-play player_btn-lg" :class="{'play-pause':isPlay}" @click="toggleStatus()"></i>
                <i class="detail_player-btn play-next player_btn-sm" @click="next()"></i>
            </div>
        </div>
    </div>
</template>

<script type="es6">
import { mapGetters } from 'vuex'

export default {
    filters:{
        //时间格式化
        time(value){
            var length = Math.floor( parseInt (value) );
            var minute = Math.floor( value / 60 );
            if(minute < 10){
                minute = '0' + minute;
            }
            var second = length % 60;
            if(second < 10){
                second = '0' + second;
            }
            return minute + ':' + second;
        }
    },
    computed:{
        ...mapGetters(['detailPlayerFlag','audio','isPlay']),
        songLrc(){
            if(this.audio.lrc){
                var temp = this.audio.lrc.split( '\r\n' );
                temp = temp.splice( 0, temp.length - 1 );
                temp = temp.map((value) => {
                    var time = value.substr(1,5);//时间格式[01:01.01],取01:01的时间进行计算
                    var seconds = parseInt( time.split( ':' )[0] ) * 60 + parseInt( time.split( ':' )[1] );//去除":",并且计算总的秒数
                    var lrcContent = value.substr(10);//从第10个字符开始取是歌词,因为前9个都是时间,类似于[02:47.12]
                    return {
                        seconds,
                        lrcContent
                    }
                });
                return temp;
            }
        },
        //距离顶部的margin值,-20px/一句歌词
        lrcOffset(){
            if(this.songLrc){
                //总长度减去所有的歌曲长度,再减2是为了每次向上移动一行歌词,而不是三行一起移动
                var offset = ( this.songLrc.length - document.querySelectorAll('.isCurrentLrc').length - 2 ) * (-20);
                return this.audio.currentLength + offset - this.audio.currentLength;
            }
        }
    },
    methods:{
        hideDetailPlayer(){
            this.$store.commit('showDetailPlayer',false)
        },
        rangeChange(event){
            var offset = event.offsetX;//鼠标指针的水平坐标
            var rangeWidth = (document.documentElement.clientWidth - 20) * 0.8 - 20;//220px,113px的长加7px的margin-right
            var clickLength = Math.floor( offset * this.audio.songLength / rangeWidth);//点击的地方距起点的秒数
            if(offset < rangeWidth){
                this.$store.commit('setAudioTime',clickLength);//将拖动的时间传给函数,从而改变歌曲播放的时间
                this.$store.commit('setCurrent',true);
            }
        },
        toggleStatus(){
            if(this.isPlay){
                document.getElementById('audioPlay').pause();
            }else{
                document.getElementById('audioPlay').play();
            }
            this.$store.commit('isPlay',!this.isPlay);
        },
        next(){
            this.$store.dispatch('next');
        },
        prev(){
            this.$store.dispatch('prev');
        } 
    }
}
</script>


