<template>
    <div class="header">
        <HeadLogo/>
        <RankHead v-if="head.toggle" :title="head.title" :cStyle="head.style"/>
        <HeadNav v-else/>
    </div>
</template>

<script>
import HeadLogo from './HeadLogo';
import RankHead from './RankHead'
import HeadNav from './HeadNav';

import {mapGetters} from 'vuex'
export default {
    //通过初始化属性，确保该属性在数据选项或基于类的组件中是被动的
    computed:{
        ...mapGetters(['head'])
    },
    components:{
        HeadLogo,
        RankHead,
        HeadNav
    }
}
</script>

<style>
    
</style>


