<template>
    <div>
    <div class="search-panel">
      <div class="search-input">
        <span class="search-icon"></span>
        <input type="text" placeholder="歌手/歌名/拼音" v-model="keyword" @keydown.enter="search">
      </div>
      <a href="javascript:;" class="search-btn" @click="search">搜索</a>
    </div>

    <div class="search-list" v-show="togglePanel">
      <div class="search-list-title">最近热门</div>
      <mt-cell v-for="(item,index) in hotList" :key="index" :title="item.keyword"></mt-cell>
    </div>

    <div class="songs-list" v-show="!togglePanel">
      <div class="search-total">
        共有{{ total }}条搜索结果
      </div>
      <mt-cell v-for="(item,index) in songList" :key="index" :title="item.filename" @click.native="playAudio(index)">
        <img src="../assets/images/download_icon.png" width="20" height="20">
      </mt-cell>
    </div>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { PLAY_AUDIO } from '../assets/js/mixin.js'

export default {
    mixins:[ PLAY_AUDIO ],
    data(){
        return {
            hotList:[],
            keyword:'',
            songList:[],
            total:'0',
            togglePanel:true //最近热门默认是显示
        }
    },
    created(){
        this.getList();
    },
    methods:{
        getList(){
            Indicator.open({
                text: '加载中...',
                spinnerType: 'snake'
            });
            this.$http.get('/searchList/api/v3/search/hot?format=json&plat=0&count=30').then(({data}) => {
                Indicator.close();
                this.hotList = data.data.info
            })
        },
        search(){
            this.togglePanel = false;
            Indicator.open({
                text: '加载中...',
                spinnerType: 'snake'
            });
            if(this.keyword){
                this.$http.get(`/searchList/api/v3/search/song?format=json&keyword=${this.keyword}&page=1&pagesize=20&showtype=1`).then(({data}) => {
                    Indicator.close();
                    this.songList = data.data.info;
                    this.total = data.data.total;
                    console.log(data);
                })
            }
        }
    }
}
</script>


