<template>
    <div class="plist">
        <mt-cell v-for="(item,index) in list" :key="index" :title="item.specialname" :to="`/plist/list/${item.specialid }`" :label="String(item.playcount)" is-link>
            <img slot="icon" :src="item.imgurl.replace('{size}','400')" width="60" height="60">
        </mt-cell>
    </div>
</template>

<script>
import { Indicator } from 'mint-ui';

export default {
    data(){
        return {
            list:[]
        }
    },
    created(){
        this.getList();
    },
    methods:{
        getList(){
            Indicator.open({
                text: '加载中...',
                spinnerType: 'snake'
            });
            this.$http.get('/indexList/plist/index&json=true').then(({data}) => {
                Indicator.close();
                this.list = data.plist.list.info;
            });
        }
    }
}
</script>

<style>
    
</style>


