<template>
    <div class="rank-info-content">
		<div class="rank-banner-wrap" :style="{backgroundImage:`url(${imgUrl})`}">
			<div class="rank-status container">
				<p>上次更新时间：{{getToday()}}</p>
			</div>
		</div>
		
		<div class="rank-info-list">
			<mt-cell v-for="(item,index) in songList" :key="index" :title="item.filename" @click.native="playAudio(index)">
				<span class="rank-index":class="{'rank-list-good' : index<3, 'rank-list-first' : index==0, 'rank-list-second' : index==1, 'rank-list-third' : index==2}">{{index+1}}</span>
				<img src="../assets/images/download_icon.png" width="20" height="20">
			</mt-cell>
		</div>
	</div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { PLAY_AUDIO } from '../assets/js/mixin.js'

export default {
    mixins:[PLAY_AUDIO],
    data(){
        return {
            imgUrl:'',
            opacity:0,
            songList:[]
        }
    },
    beforeRouteEnter(to, from, next){
        next(vm => {
            vm.getRankinfo();
            vm.$store.commit('showHead',true);
            window.onscroll = () => {
                vm.opacity = window.pageYOffset / 250;
                vm.$store.commit('setHeadStyle', {background: `rgba(43,162,251,${vm.opacity})`});
            }
        });
    },
    beforeRouteLeave(to, from, next){
        next();
        this.$store.commit('showHead',false);
        window.onscroll = null;
    },
    methods:{
        getToday(){
            var time = new Date();
            var year = time.getFullYear();
            var month = time.getMonth() + 1;
            var day = time.getDate();
            if(month < 10) month = '0' + month;
            if(day < 10) day = '0' + day;
            return `${year}-${month}-${day}`;
        },
        getRankinfo(){
            Indicator.open({
                text: '加载中...',
                spinnerType: 'snake'
            });
            var infoID = this.$route.params.id;
            this.$http.get(`/indexList/rank/info/?rankid=${infoID}&page=1&json=true`).then(({data}) => {
                Indicator.close();
                this.imgUrl = data.info.banner7url.replace('{size}','400');
                this.songList = data.songs.list;
                const info = data.info;
                this.$store.commit('setHeadTitle',info.rankname);
            });
        }
    }
}
</script>

<style>
   .rank-list-good {
		display: inline-block;
		padding: 2px 8px;
		left: 12px !important;
		border-radius: 8px;
		color: #fff;
		margin-top: 3px !important;
	}
	
	.rank-list-first {
		background-color: #E80000;
	}
	
	.rank-list-second {
		background-color: #FF7200;
	}
	
	.rank-list-third {
		background-color: #F8B300;
	} 
</style>


