// 引入 axios
import axios from 'axios'

const state = {
    audio:{
        songUrl: '',
        imgUrl: 'http://m.kugou.com/v3/static/images/index/logo_kugou.png',
        title: '',
        singer: '',
        currentLength: 0,
        songLength: 0,
        currentFlag: false
    },//歌曲展示的所有内容
    showPlayer:false,//在新页面中展示完整的音乐信息
    isPlay:true,//点击歌曲即播放音乐
    listInfo:{
        songList:[],
        songIndex:0
    },//歌曲的信息
    listenCount:0,//播放的时间0秒,即刚进入应用不播放
    audioLoadding:false,//在未获得歌曲信息前等待
    detailPlayerFlag:false,//音乐播放详情页默认不展示
}
const getters = {
    audio(state){
        return state.audio
    },
    showPlayer(state){
        return state.showPlayer
    },
    isPlay(state){
        return state.isPlay
    },
    audioLoadding(state){
        return state.audioLoadding
    },
    detailPlayerFlag(state){
        return state.detailPlayerFlag
    }
}
const mutations = {
    setListInfo(state, { list, index }){
        state.listInfo.songList = list;
        state.listInfo.songIndex = index;
    },
    isPlay(state, flag){
        state.isPlay = flag
    },
    setAudio(state, audio){
        if(!state.listenCount){
            state.showPlayer = true //首次进入应用时不可打开播放详情
        }
        state.listenCount++;
        state.audio = {...(state.audio),...audio} //原先的audio数据，和现在改变后的audio数据拼接，相同的改变后的覆盖之前的
    },
    showDetailPlayer(state, flag){
        state.detailPlayerFlag = flag
    },
    setLrc(state, lrc){
        state.audio = {...(state.audio), lrc}
    },
    setAudioTime(state, time){
        state.audio.currentLength = time
        // console.log(state.audio.currentLength);
    },
    setCurrent(state, flag){
        state.audio.currentFlag = flag			  
    },
    toggleAudioLoadding(state, flag){
        state.audioLoadding = flag
    }
}
const actions = {
    getSong({ commit, state }, hash){
        commit('toggleAudioLoadding',true);//未加载完成的圆圈
        axios.get(`/playList/yy/index.php?r=play/getdata&hash=${hash}`).then(({data}) => {
            data = data.data;
            const songUrl = data.play_url;
            const imgUrl = data.img;
            const singer = data.author_name;
            const title = data.audio_name;
            const currentLength = 0;
            const songLength = data.timelength / 1000;
            const lrc = data.lyrics;//歌的进度条

            const audio = { songUrl, imgUrl, singer, title, songLength, currentLength};

            commit('setAudio', audio);
            commit('setLrc', lrc);
            commit('toggleAudioLoadding', false);//加载完成圆圈消失
        });
    },
    next({ dispatch, state }){
        var list = state.listInfo.songList;
        if(state.listInfo.songIndex == list.length - 1){
            state.listInfo.songIndex == 0;
        }else{
            ++state.listInfo.songIndex;
        }
        var hash = list[state.listInfo.songIndex].hash;
        dispatch('getSong', hash);
    },
    prev({ dispatch, state }){
        var list = state.listInfo.songList;
        if(state.listInfo.songIndex == list.length - 1){
            state.listInfo.songIndex == 0;
        }else{
            state.listInfo.songIndex--;
        }
        var hash = list[state.listInfo.songIndex].hash;
        dispatch('getSong', hash);
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}


