import Vue from 'vue'
import Router from 'vue-router'
import newSongs from '../views/newSongs.vue'
import rank from '../views/rank.vue'
import rankInfo from '../views/rankInfo.vue'
import plist from '../views/plist.vue'
import plistInfo from '../views/plistInfo.vue'
import singer from '../views/singer.vue'
import singerList from '../views/singerList.vue'
import singerInfo from '../views/singerInfo.vue'
import search from '../views/search.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/newSongs',
      component:newSongs,
      //增加别名
      alias: '/'
    },
    {
      path:'/rank',
      component:rank
    },
    {
      path:'/rank/info/:id',
      component:rankInfo
    },
    {
      path:'/plist',
      component:plist
    },
    {
      path:'/plist/list/:id',
      component:plistInfo
    },
    {
      path:'/singer',
      component:singer
    },
    {
      path:'/singer/list/:id',
      component:singerList
    },
    {
      path:'/singer/info/:id',
      component:singerInfo
    },
    {
      path:'/search',
      component:search
    },
    {
      path:'*',
      redirect:'/'
    }
  ]
})


