<template>
    <div class="rank-info-content plist-info">
        <div class="rank-banner-wrap" :style="{backgroundImage:`url(${imgUrl})`}"></div>
        <div class="plist-desp container">
            <p class="plist-desp-p" :class="{'plist-desp-hide': hideIntro }">{{intro}}</p>
            <img src="../assets/images/open_icon.png" class="plist-desp-icon" @click="toggleIntro" v-if="hideIntro"> 
            <img src="../assets/images/close_icon.png" class="plist-desp-icon" @click="toggleIntro" v-else> 
        </div>
        <div class="plist-desp-bottom" style="width: 100%;height: 5px;background-color: #f1f1f1"></div>
        <div class="rank-info-list">
            <mt-cell v-for="(item,index) in songList" :key="index" :title="item.filename" @click.native="playAudio(index)">
                <img src="../assets/images/download_icon.png" width="20" height="20">
            </mt-cell>
        </div>
    </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { PLAY_AUDIO } from '../assets/js/mixin.js'

export default {
    mixins:[PLAY_AUDIO],
    data(){
        return {
            imgUrl:'',
            intro:'',
            hideIntro:true,
            songList:[],
            opacity:0
        }
    },
    beforeRouteEnter(to, from, next){
        next(vm => {
            vm.$store.commit('showHead',true);
            vm.getInfolist();
            window.onscroll = () => {
                vm.opacity = window.pageYOffset / 250;
                vm.$store.commit('setHeadStyle', {background: `rgba(43,162,251,${vm.opacity})`});
            }
        });
    },
    beforeRouteLeave(to, from, next){
        this.$store.commit('showHead',false);
        window.onscroll = null;
        next();
    },
    methods:{
        getInfolist(){
            Indicator.open({
                text: '加载中...',
                spinnerType: 'snake'
            });
            var infoID = this.$route.params.id;
            this.$http.get(`indexList/singer/info/${infoID}&json=true`).then(({data}) => {
                Indicator.close();
                this.imgUrl = data.info.imgurl.replace('{size}','400');
                this.intro = data.info.intro;

                const songList = data.songs.list;
                this.songList = songList;

                const info = data.info;
                this.$store.commit('setHeadTitle',info.singername);
            });
        },
        toggleIntro(){
            this.hideIntro = !this.hideIntro;
        }
    }
}
</script>

<style>
.rank-banner-wrap {
    height: 250px;
}   
</style>


