const state = {
    headNav: 'head-nav1',
    head: {
        toggle: false,
        title: '',
        style: {'background': 'rgba(43,162,251,0)'}
    },
}
const getters = {
    headNav(state){
        return state.headNav;
    },
    head(state){
        return state.head;
    }
}
const mutations = {
    setHeadNav(state,nav){
        state.headNav = nav;
    },
    showHead(state,flag){
        state.head.toggle = flag;
    },
    setHeadStyle(state,style){
        state.head.style = style;
    },
    resetHeadStyle(state,style){
        state.head.state = {'background':'rgba(43,162,251,0)'}
    },
    setHeadTitle(state,title){
        state.head.title = title;
    }
}
const actions = {
    
}

export default {
    state,
    getters,
    mutations,
    actions
}


